/* Define your primary color */
:root {
  --primary-color: #EE5A52; /* Your primary red color */
  --primary-hover: #FEECED; /* Light hover color */
  --primary-light: rgb(247, 214, 214); /* Light shade for hover backgrounds */
}

.date-picker {
  background: #fff;
  border: 1px solid #e2e8f0;
  border-radius: 8px;
  padding: 8px 12px;
  width: 100%;
  font-size: 14px;
}

.calendar {
  background: #fff;
  border: 1px solid #e2e8f0;
  border-radius: 8px;
  font-family: inherit;
}

/* Header styling */
.react-datepicker__header {
  background: var(--primary-color);
  border-bottom: 1px solid var(--primary-color);
  color: white;
}

.react-datepicker__current-month {
  color: white;
}

.react-datepicker__navigation {
  top: 10px;
}

.react-datepicker__navigation--previous {
  border-right-color: white;
}

.react-datepicker__navigation--next {
  border-left-color: white;
}

.react-datepicker__day-name {
  color: white;
}

/* Selected day styling - highest priority */
.react-datepicker__day--selected {
  background: var(--primary-color) !important;
  color: white !important;
  border-radius: 4px;
}

.react-datepicker__day--selected:hover {
  background: var(--primary-color) !important;
  color: white !important;
}

/* Range styling */
.react-datepicker__day--in-range {
  background: var(--primary-light) !important;
  color: black !important;
  border-radius: 0;
}

.react-datepicker__day--in-range:hover {
  background: var(--primary-hover) !important;
  color: var(--primary-color) !important;
}

/* Range start and end - should override in-range */
.react-datepicker__day--range-start {
  background: var(--primary-color) !important;
  color: white !important;
  border-radius: 4px 0 0 4px;
}

.react-datepicker__day--range-start:hover {
  background: var(--primary-color) !important;
  color: white !important;
}

.react-datepicker__day--range-end {
  background: var(--primary-color) !important;
  color: white !important;
  border-radius: 0 4px 4px 0;
}

.react-datepicker__day--range-end:hover {
  background: var(--primary-color) !important;
  color: white !important;
}

/* When start and end are the same day */
.react-datepicker__day--range-start.react-datepicker__day--range-end {
  border-radius: 4px;
  background: var(--primary-color) !important;
  color: white !important;
}

.react-datepicker__day--range-start.react-datepicker__day--range-end:hover {
  background: var(--primary-color) !important;
  color: white !important;
}

/* Today styling */
.react-datepicker__day--today {
  background: #f7fafc;
  color: var(--primary-color);
  font-weight: bold;
}

.react-datepicker__day--today:hover {
  background: var(--primary-light);
  color: var(--primary-color);
}

/* Hover states for regular days */
.react-datepicker__day:hover {
  background: var(--primary-light);
  color: var(--primary-color);
  border-radius: 4px;
}

/* Disabled days */
.react-datepicker__day--disabled {
  color: #cbd5e0;
  cursor: not-allowed;
}

.react-datepicker__day--disabled:hover {
  background: transparent;
  color: #cbd5e0;
}

/* Outside month days */
.react-datepicker__day--outside-month {
  color: #cbd5e0;
}

.react-datepicker__day--outside-month:hover {
  background: #f7fafc;
  color: #a0aec0;
}
.react-datepicker__day--in-range
{
  background-color: var(--primary-color) !important;
}