
@import 'react-day-picker/style.css';

.rdp {
  --rdp-cell-size: 40px;
  --rdp-accent-color: #F30F1D !important;
  --rdp-accent-background-color: #F30F1D !important;
  --rdp-day-height: 40px;
  --rdp-day-width: 40px;
  --rdp-day_button-height: 38px;
  --rdp-day_button-width: 38px;
  --rdp-selected-border: 2px solid #F30F1D !important;
  --rdp-today-color: #F30F1D !important;
  --rdp-range_start-background: #F30F1D !important;
  --rdp-range_start-color: white !important;
  --rdp-range_end-background: #F30F1D !important;
  --rdp-range_end-color: white !important;
  --rdp-range_middle-background-color: rgba(243, 15, 29, 0.1) !important;
  --rdp-range_middle-color: #F30F1D !important;
  --rdp-outline: 2px solid #F30F1D !important;
  --rdp-outline-selected: 2px solid #F30F1D !important;
  --rdp-nav_button-height: 32px;
  --rdp-nav_button-width: 32px;
  --rdp-disabled-opacity: 0.5;
  --rdp-outside-opacity: 0.5;
  --rdp-animation_duration: 0.2s;
  --rdp-animation_timing: ease-in-out;
  margin: 0;
  font-family: inherit;
}
.rdp-root {
  --rdp-accent-color:red; 
  --rdp-range_middle-background-color:#FEECED
}
.rdp-months {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.rdp-month {
  margin: 0;
}

.rdp-caption {
  display: flex;
  justify-content: center;
  padding: 0.5rem;
  margin-bottom: 0.5rem;
  color: #1a202c;
  font-weight: 600;
}

.rdp-nav {
  display: flex;
  gap: 0.5rem;
}

.rdp-nav_button {
  display: flex;
  align-items: center;
  justify-content: center;
  width: var(--rdp-nav_button-width);
  height: var(--rdp-nav_button-height);
  border: none;
  background: none;
  cursor: pointer;
  border-radius: 4px;
  color: #4a5568;
  font-size: 14px;
  transition: all 0.2s;
}

.rdp-nav_button:hover {
  background-color: #f7fafc;
  color: #F30F1D;
}

.rdp-nav_button:disabled {
  opacity: var(--rdp-nav_button-disabled-opacity, 0.5);
  cursor: not-allowed;
}

.rdp-table {
  width: 100%;
  border-collapse: collapse;
}

.rdp-head_row {
  margin-bottom: 0.5rem;
}

.rdp-head_cell {
  font-weight: 600;
  font-size: 12px;
  color: #4a5568;
  text-align: center;
  padding: 0.25rem;
  text-transform: uppercase;
  letter-spacing: 0.05em;
}

.rdp-tbody {
  border: none;
}

.rdp-row {
  border: none;
}

.rdp-cell {
  text-align: center;
  padding: 2px;
  border: none;
}

.rdp-button {
  display: flex;
  align-items: center;
  justify-content: center;
  width: var(--rdp-day_button-width);
  height: var(--rdp-day_button-height);
  border: none;
  background: none;
  cursor: pointer;
  border-radius: 6px;
  font-size: 14px;
  transition: all 0.2s;
  color: #1a202c;
  font-weight: 500;
}

.rdp-button:hover {
  background-color: #f7fafc;
  color: #F30F1D;
}

.rdp-button:disabled {
  opacity: var(--rdp-disabled-opacity);
  cursor: not-allowed;
}

.rdp-day_today {
  font-weight: 700;
  color: var(--rdp-today-color);
}

.rdp-day_selected {
  background-color: var(--rdp-accent-background-color) !important;
  color: white !important;
  font-weight: 700;
}

.rdp-day_selected:hover {
  background-color: #d00e19 !important;
  color: white !important;
}

.rdp-day_range_start,
.rdp-day_range_end {
  background-color: var(--rdp-range_start-background) !important;
  color: var(--rdp-range_start-color) !important;
  font-weight: 700;
}

.rdp-day_range_middle {
  background-color: var(--rdp-range_middle-background-color) !important;
  color: var(--rdp-range_middle-color) !important;
}

.rdp-day_range_middle:hover {
  background-color: rgba(243, 15, 29, 0.2) !important;
}

.rdp-day_outside {
  opacity: var(--rdp-outside-opacity);
}